.loading-gui-box{position:absolute;left:0;top:0;width:100%;height:100%;z-index:9999}.loading-gui-box .loading-box{padding:20px;position:absolute;top:0;bottom:0;left:0;right:0;margin:auto;height:16px;text-align:center}.loading-gui-box .k-line{display:inline-block;width:16px;height:16px;border-radius:16px;margin:2px}.loading-gui-box .k-line-1{animation:k-loadingP 1s infinite}.loading-gui-box .k-line-2{animation:k-loadingP 1s infinite;animation-delay:0.1s}.loading-gui-box .k-line-3{animation:k-loadingP 1s infinite;animation-delay:0.2s}.loading-gui-box .k-line-4{animation:k-loadingP 1s infinite;animation-delay:0.3s}.loading-gui-box .k-line-5{animation:k-loadingP 1s infinite;animation-delay:0.4s}@keyframes k-loadingP{0%{transform:translateY(0)}35%{transform:translateY(0);opacity:0.3}50%{transform:translateY(-10px);opacity:0.8}70%{transform:translateY(3px);opacity:0.8}85%{transform:translateY(-3px)}}
